import { Injectable } from '@angular/core';
import { Query, Apollo } from 'apollo-angular';
import gql from 'graphql-tag';

/* Export type definitions for the response */
export interface Film {
  episodeId: number;
  title: string;
}

export interface Response {
  allFilms: Film[];
}

/*
 * Extend the apollo angular Query service,
 * See: https://www.apollographql.com/docs/angular/basics/services
 */
@Injectable({
  providedIn: 'root'
})
export class AllFilmsGQL extends Query<Response> {
  document = gql`
    query getAllFilms($first: Int!) {
      allFilms(first: $first) {
        episodeId
        title
      }
    }
  `;
}
