import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { GraphqlExampleComponent } from './graphql-example/pages/graphql-example/graphql-example.component';
import { HomeComponent } from './home/pages/home/home.component';
import { MockExampleComponent } from './mock-example/mock-example.component';
import { WindowRefComponent } from './window-ref/window-ref.component';
import { PipesShowcaseComponent } from './pipes-showcase/pipes-showcase.component';
import { TranslationComponent } from './translation/translation.component';
import { FormsExampleComponent } from './forms/forms-example.component';
import { BootstrapGridUsecasesComponent } from './bootstrap-grid-usecases/bootstrap-grid-usecases.component';
import { CssGridUsecasesComponent } from './css-grid-usecases/css-grid-usecases.component';

const routes: Routes = [
  { path: '', redirectTo: '/home', pathMatch: 'full' },
  { path: 'home', component: HomeComponent },
  { path: 'graphql-example', component: GraphqlExampleComponent },
  { path: 'mock-example', component: MockExampleComponent },
  { path: 'window-ref', component: WindowRefComponent },
  { path: 'pipes-showcase', component: PipesShowcaseComponent },
  { path: 'translation', component: TranslationComponent },
  { path: 'forms-example', component: FormsExampleComponent },
  { path: 'bootstrap-grid-usecases', component: BootstrapGridUsecasesComponent },
  { path: 'css-grid-usecases', component: CssGridUsecasesComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  declarations: [],
  exports: [RouterModule]
})
export class AppRoutingModule {}
