import { I18NEXT_SERVICE, ITranslationService, I18NextPipe } from 'angular-i18next';
import { Component, OnInit, Inject } from '@angular/core';

@Component({
  selector: 'hdms-translation',
  templateUrl: './translation.component.html',
  styleUrls: ['./translation.component.scss']
})
export class TranslationComponent implements OnInit {
  count = 0;
  gender = 'male';
  languageString: string;

  constructor(
    @Inject(I18NEXT_SERVICE) private readonly i18NextService: ITranslationService,
    private readonly i18NextPipe: I18NextPipe
  ) {}

  ngOnInit() {
    this.languageString = this.i18NextPipe.transform('examples:examples.dummy');
  }

  getLanguage() {
    return this.i18NextService.language;
  }

  increaseCount() {
    this.count += 1;
  }

  decreaseCount() {
    this.count -= 1;
  }

  setGender(gender) {
    this.gender = gender;
  }
}
