import { Component, OnInit } from '@angular/core';
import { Apollo } from 'apollo-angular';
import gql from 'graphql-tag';

/* Query */

const FILMS_LIST_QUERY = gql`
  query AllFilmsGQL {
    allFilms(first: 4, orderBy: episodeId_ASC) {
      episodeId
      title
    }
  }
`;

/* Type definitions */
interface Film {
  episodeId: number;
  title: string;
}

interface Response {
  allFilms: Film[];
}

/* Component */
@Component({
  selector: 'hdms-graphql-showcase',
  templateUrl: './graphql-showcase.component.html',
  styleUrls: ['./graphql-showcase.component.scss']
})
export class GraphqlShowcaseComponent implements OnInit {
  films: Film[];
  loading = true;

  constructor(private readonly apollo: Apollo) {}

  ngOnInit() {
    this.apollo
      .watchQuery<Response>({
        query: FILMS_LIST_QUERY
      })
      .valueChanges.subscribe(({ data, loading }) => {
        this.loading = loading;
        this.films = data.allFilms;
      });
  }
}
