import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'currencyFormat'
})
export class CurrencyFormatPipe implements PipeTransform {
  /**
   * used for global pattern assignment
   */
  static Pattern: string;

  readonly patternRegex: RegExp = /^([^#]*)(#+[^#]?#*[^#]?#+)([^#]*)$/;

  private static getIntValue(value: any, decimalPlaces: number): number {
    return parseInt(decimalPlaces ? value : Math.round(value), 10);
  }

  private static getDecimalPattern(separator: string, pattern: string): string {
    return separator ? pattern.substr(pattern.lastIndexOf(separator) + 1) : '';
  }

  private static getThousandSeparatorPos(value: string): number {
    return value.length > 3 ? value.length % 3 : 0;
  }

  private static getRoundedValue(value: number, factor: number): number {
    return Math.round(Math.abs(value) * factor) / factor;
  }

  private static getThousandPart(value: string, separator: string, separatorPos: number): string {
    return separatorPos ? value.substr(0, separatorPos) + separator : '';
  }

  private static getHundredPart(value: string, separator: string, separatorPos: number): string {
    return value.substr(separatorPos).replace(/(\d{3})(?=\d)/g, `$1${separator}`);
  }

  private static getDecimalPart(value: number, amount: number, separator: string): string {
    return amount ? separator + value.toFixed(amount).slice(2) : '';
  }

  /**
   * Transforms a numeric value to a given currency format
   * @param {any} value - string or number
   * @param {string} customPattern - optionally/individually applied
   */
  transform(value: any, customPattern?: string): string {
    const pattern: string = customPattern || CurrencyFormatPipe.Pattern;

    // return the input in case of invalid pattern or value
    if (!pattern || !pattern.match(this.patternRegex) || !value || isNaN(value)) {
      return value;
    }

    return pattern.replace(this.patternRegex, (all, preFix, numeralPattern, postFix) => {
      const separators: Array<string> = numeralPattern.match(/[^#]+/g) || [],
        thousandSeparator: string = separators[0] || '',
        decimalSeparator: string = separators.length > 1 ? separators[1] : '',
        decimalPattern: string = CurrencyFormatPipe.getDecimalPattern(decimalSeparator, numeralPattern),
        decimalPlaces: number = decimalPattern ? decimalPattern.length : 0,
        valueInt: number = CurrencyFormatPipe.getIntValue(value, decimalPlaces),
        valueAbs: number = Math.abs(valueInt),
        valueAbsString: string = valueAbs.toString(),
        thousandSeparatorPos: number = CurrencyFormatPipe.getThousandSeparatorPos(valueAbsString),
        roundedValue: number = CurrencyFormatPipe.getRoundedValue(value, Math.pow(10, decimalPlaces)),
        negativeSign: string = valueInt < 0 ? '-' : '',
        thousandPart: string = CurrencyFormatPipe.getThousandPart(
          valueAbsString,
          thousandSeparator,
          thousandSeparatorPos
        ),
        hundredPart: string = CurrencyFormatPipe.getHundredPart(
          valueAbsString,
          thousandSeparator,
          thousandSeparatorPos
        ),
        decimalPart: string = CurrencyFormatPipe.getDecimalPart(
          roundedValue - valueAbs,
          decimalPlaces,
          decimalSeparator
        );

      return preFix + negativeSign + thousandPart + hundredPart + decimalPart + postFix;
    });
  }
}
