import { Component } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { CustomValidators } from '@hdms/shared/ui-core';

@Component({
  selector: 'hdms-forms-example',
  templateUrl: './forms-example.component.html'
})
export class FormsExampleComponent {
  readonly fData: any = {
    labelTop: 'Prefilled text',
    labelLeft: '',
    postcode: '',
    city: '',
    message: '',
    counter: 'two',
    check0: false,
    check1: true,
    check2: false
  };

  reactiveForm: FormGroup;

  constructor(fb: FormBuilder) {
    this.reactiveForm = fb.group({
      labelTop: [this.fData.labelTop],
      labelLeft: [
        this.fData.labelLeft,
        [Validators.required, Validators.minLength(10), Validators.pattern(/[a-zA-Z]*/)]
      ],
      address: fb.group({
        postcode: [
          this.fData.postcode,
          [Validators.required, Validators.minLength(5), Validators.maxLength(6), Validators.pattern('[0-9]*')]
        ],
        city: [this.fData.city, [Validators.required, Validators.minLength(4), Validators.pattern(/[a-zA-Z]*/)]]
      }),
      counter: [this.fData.counter, [Validators.required]],
      check0: [this.fData.check0, [CustomValidators.requiredTrue]],
      check1: [this.fData.check1, [CustomValidators.requiredTrue]],
      check2: [{ value: this.fData.check2, disabled: true }],
      message: [this.fData.message, [Validators.required]]
    });
  }

  onSubmit(value: any) {
    console.log(value);
  }
}
