import { Component, OnInit } from '@angular/core';
import { MockExampleService } from './mock-example.service';

@Component({
  selector: 'hdms-mock-example',
  templateUrl: './mock-example.component.html',
  styleUrls: ['./mock-example.component.scss']
})
export class MockExampleComponent implements OnInit {
  priceResponseData = [];
  errorMessageData = [];

  constructor(pricingService: MockExampleService) {
    this.priceResponseData = pricingService.getPrices();
    this.errorMessageData = pricingService.getErrors();
  }

  ngOnInit() {}
}
